import React from 'react';
import Sync from 'react-syncing';
import { withStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import ButtonTableTraining from './ButtonTableTraining';





let id = 0;
function createData(date, sesion, duracion) {
    id += 1;
    return { id, date, sesion, duracion };
}

const rows = [
    createData('11/09/2018', 'Maana', 60),
    createData('12/09/2018', 'Tarde', 75),
    createData('13/09/2018', 'Maana', 45),
    createData('14/09/2018', 'Maana', 50),
    createData('15/09/2018', 'Partido', 120),
];

export default class TrainingsPlayer extends Sync {
    constructor() {
        super()

        this.getPlayerClub = this.getPlayerClub.bind(this);

        this.state = {
            trainings: [],

        }
    }

    didMount() {
        this.getPlayerClub()
    }

    getPlayerClub() {
        const url = `http://localhost:8000/`;
        fetch(url, {
            method: 'GET',
            credentials: "include",
            headers: {
                'Content-Type': 'application/json'
            }

        })
            .then((res) => res.json())
            .then(res => {

                this.set({
                    trainings: res.result
                })
                console.log(res.result);
            })
            .catch(error => {
                console.log(error)
            })
    }
    render() {

        return (
            <Paper style={{
                width: '100%',
                marginTop: '100px',
                overflowX: 'auto',
            }}>
                <Table style={{ minWidth: 700 }} key={id}>
                    <TableHead>
                        <TableRow>
                            <TableCell>Fecha</TableCell>
                            <TableCell numeric>Sesin</TableCell>
                            <TableCell numeric>Duracin</TableCell>
                            <TableCell numeric></TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {rows.map(row => {
                            return (
                                <TableRow style={{ backgroundColor: "default" }} key={row.id} >
                                    <TableCell component="th" scope="row">
                                        {row.date}
                                    </TableCell>
                                    <TableCell numeric>{row.sesion}</TableCell>
                                    <TableCell numeric>{row.duracion}</TableCell>
                                    <TableCell numeric><ButtonTableTraining /></TableCell>
                                </TableRow>
                            );
                        })}
                    </TableBody>
                </Table>
            </Paper>
        );
    }
}





