import React from 'react';
import Sync from 'react-syncing';
import Radio from '@material-ui/core/Radio';


export default class RadioButtonSleepQuality extends Sync {

    constructor() {
        super()
        this.state = {
            selectedValue: '1',
            selectedValue: '2',
            selectedValue: '3',
            selectedValue: '4',
            selectedValue: '5',
        };
    }

    handleChange(selectedValue) {
        return event => {
            this.set({
                [selectedValue]: event.target.value
            });
        };
    }

    render() {

        return (
            <div>
                <Radio
                    checked={this.state.selectedValue === '1'}
                    onChange={this.handleChange}
                    value="1"
                    name="radio-button-demo"
                    aria-label="1"
                />
                <Radio
                    checked={this.state.selectedValue === '2'}
                    onChange={this.handleChange}
                    value="2"
                    name="radio-button-demo"
                    aria-label="2"
                />
                <Radio
                    checked={this.state.selectedValue === '3'}
                    onChange={this.handleChange}
                    value="3"
                    name="radio-button-demo"
                    aria-label="3"

                />
                <Radio
                    checked={this.state.selectedValue === '4'}
                    onChange={this.handleChange}
                    value="4"
                    color="default"
                    name="radio-button-demo"
                    aria-label="4"
                />
                <Radio
                    checked={this.state.selectedValue === '5'}
                    onChange={this.handleChange}
                    value="5"
                    color="default"
                    name="radio-button-demo"
                    aria-label="5"
                />

            </div>
        );
    }
}

