import React from 'react';
import Sync from 'react-syncing';
import { TextField, Button } from '@material-ui/core';
import { Redirect } from "react-router-dom";


class Login extends Sync {

    constructor() {
        super()
        this.handleChange = this.handleChange.bind(this);
        this.handleLogin = this.handleLogin.bind(this);

        this.state = {
            email: '',
            password: ''
        }
    }


    handleChange(name) {
        return event => {
            this.set({
                [name]: event.target.value,
            });
        };
    }

    handleLogin() {
        const url = `http://localhost:8000/users/loginPersona`;

        fetch(url, {
            method: 'post',
            credentials: "include",
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                email: this.state.email,
                password: this.state.password
            })
        })
            .then((res) => res.json())
            .then((res) => {

                if (res.message) {
                    this.set({ logged: true })
                } else {
                    this.set({ error: true, errorMsg: res.error })
                }
            })
            .catch(console.log)
    }

    render() {

        return (
            <div>
                {this.state.logged ? (
                    <Redirect to="/home" />
                ) : (

                        <form style={{
                            display: 'flex',
                            flexWrap: 'wrap',
                        }} noValidate autoComplete="off">

                            <TextField
                                error={this.state.error ? true : false}
                                helperText={this.state.error ? this.state.errorMsg : ""}
                                label="Email"
                                style={{

                                    width: 200,
                                }}
                                value={this.state.email}
                                onChange={this.handleChange('email')}
                                margin="normal"
                            />

                            <TextField

                                label="Password"
                                style={{

                                    width: 200,
                                }}
                                type="password"
                                value={this.state.password}
                                onChange={this.handleChange('password')}
                                margin="normal"
                            />
                            <Button onClick={this.handleLogin} variant="contained">Login</Button>
                        </form>
                    )}
            </div>

        )


    }
}

export default Login;
