import React from 'react';
import Sync from 'react-syncing'
import { TextField, Button, MenuItem } from '@material-ui/core';


const sesion = [
    {
        value: 'morning',
        label: 'Maana',
    },
    {
        value: 'afternoon',
        label: 'Tarde',
    },
    {
        value: 'game',
        label: 'Partido',
    },
];


export default class FormTrainingStaff extends Sync {

    constructor() {
        super()
        this.handleChange = this.handleChange.bind(this);
        this.handleRegisterTraining = this.handleRegisterTraining.bind(this);


        this.state = {
            fecha: '',
            sesion: '',
            duracion: '',

        }


    }

    handleRegisterTraining() {
        const url = `http://localhost:8000/addTrainingStaff`;

        fetch(url, {
            method: 'post',
            credentials: "include",
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                fecha: this.state.fecha,
                sesion: this.state.sesion,
                duracion: this.state.duracion,


            })
        })
            .then((res) => res.json())
            .then((res) => {
                if (res.message) {
                    this.set({ ok: true })
                } else {
                    this.set({ error: true, errorMsg: res.error })
                }
            })
            .catch(console.log)
    }


    handleChange(name) {
        return event => {
            this.set({
                [name]: event.target.value,
            });
        };
    }

    render() {


        return (
            <form style={{ display: 'flex', flexWrap: 'wrap' }}>


                <TextField
                    label="Fecha de Entrenamiento"
                    value={this.state.fecha}
                    onChange={this.handleChange('fecha')}
                    type="date"
                    margin="normal"
                />
                <TextField
                    select
                    label="Sesin"
                    value={this.state.sesion}
                    onChange={this.handleChange('sesion')}

                    helperText="Seleccion tipo de sesin"
                    margin="normal"
                >
                    {sesion.map(option => (
                        <MenuItem key={option.value} value={option.value}>
                            {option.label}
                        </MenuItem>
                    ))}
                </TextField>
                <TextField
                    label="Duracin"
                    value={this.state.duracion}
                    onChange={this.handleChange('duracion')}
                    type="number"
                    margin="normal"
                />



                <Button onClick={this.handleRegisterTraining} variant="contained">Registrar</Button>

            </form >
        );
    }
}



