import React from 'react';
import Sync from 'react-syncing';
import Radio from '@material-ui/core/Radio';




class RadioButtonRPE extends Sync {
    constructor() {
        super()

        this.state = {
            selectedValue: '1',
            selectedValue: '2',
            selectedValue: '3',
            selectedValue: '4',
            selectedValue: '5',
            selectedValue: '6',
            selectedValue: '7',
            selectedValue: '8',
            selectedValue: '9',
            selectedValue: '10',
        };
    }


    handleChange(selectedValue) {
        return event => {
            this.set({
                [selectedValue]: event.target.value
            });
        };
    }

    render() {


        return (
            <div>
                <Radio
                    checked={this.state.selectedValue === '1'}
                    onChange={this.handleChange}
                    value="1"
                    name="radio-button-demo"
                    aria-label="1"
                />
                <Radio
                    checked={this.state.selectedValue === '2'}
                    onChange={this.handleChange}
                    value="2"
                    name="radio-button-demo"
                    aria-label="2"
                />
                <Radio
                    checked={this.state.selectedValue === '3'}
                    onChange={this.handleChange}
                    value="3"
                    name="radio-button-demo"
                    aria-label="3"

                />
                <Radio
                    checked={this.state.selectedValue === '4'}
                    onChange={this.handleChange}
                    value="4"
                    color="default"
                    name="radio-button-demo"
                    aria-label="4"
                />
                <Radio
                    checked={this.state.selectedValue === '5'}
                    onChange={this.handleChange}
                    value="5"
                    color="default"
                    name="radio-button-demo"
                    aria-label="5"
                />
                <Radio
                    checked={this.state.selectedValue === '6'}
                    onChange={this.handleChange}
                    value="6"
                    name="radio-button-demo"
                    aria-label="6"
                />
                <Radio
                    checked={this.state.selectedValue === '7'}
                    onChange={this.handleChange}
                    value="7"
                    name="radio-button-demo"
                    aria-label="7"
                />
                <Radio
                    checked={this.state.selectedValue === '8'}
                    onChange={this.handleChange}
                    value="8"
                    name="radio-button-demo"
                    aria-label="8"

                />
                <Radio
                    checked={this.state.selectedValue === '9'}
                    onChange={this.handleChange}
                    value="9"
                    color="default"
                    name="radio-button-demo"
                    aria-label="9"
                />
                <Radio
                    checked={this.state.selectedValue === '10'}
                    onChange={this.handleChange}
                    value="10"
                    color="default"
                    name="radio-button-demo"
                    aria-label="10"
                />


            </div>
        );
    }
}



export default RadioButtonRPE;