import React from 'react';
import Sync from 'react-syncing';
import axios from 'axios';
import { TextField, Button } from '@material-ui/core';
import ButtonSelectClub from './ButtonSelectClub';
import 'babel-polyfill';

class FormStaff extends Sync {

    constructor() {
        super()
        this.handleChange = this.handleChange.bind(this);
        this.handleRegister = this.handleRegister.bind(this);
        this.getClub = this.getClub.bind(this);

        this.state = {
            nombre: '',
            apellidos: '',
            email: '',
            password: '',
            pais: '',
            fechaNacimiento: '',
            telefono: '',
            club: '',
            clubes: [],
        }


    }
    didMount() {
        this.getClub();
    }

    getClub() {
        const url = `http://localhost:8000/club/`;
        axios.get(url)
            .then(res => {
                this.set({
                    clubes: res.data
                })
            })
            .catch(error => {
                console.log(error)
            })
    };



    handleRegister() {
        const url = `http://localhost:8000/addPersona`;

        fetch(url, {
            method: 'post',
            credentials: "include",
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                nombre: this.state.nombre,
                apellidos: this.state.apellidos,
                pais: this.state.pais,
                fechaNacimiento: this.state.fechaNacimiento,
                telefono: this.state.telefono,
                club: this.state.club,
                clubes: this.state.clubes,
                email: this.state.email,
                password: this.state.password
            })
        })
            .then((res) => res.json())
            .then((res) => {
                if (res.message) {
                    this.set({ logged: true })
                } else {
                    this.set({ error: true, errorMsg: res.error })
                }
            })
            .catch(console.log)
    }


    handleChange(name) {
        return event => {
            this.set({
                [name]: event.target.value,
            });
        };
    }

    render() {


        return (
            <form style={{ display: 'flex', flexWrap: 'wrap' }}>
                <TextField
                    label="Nombre"
                    value={this.state.nombre}
                    onChange={this.handleChange('nombre')}
                    margin="normal"
                />
                <TextField
                    label="Apellidos"
                    value={this.state.apellidos}
                    onChange={this.handleChange('apellidos')}
                    margin="normal"
                />
                <TextField
                    label="Email"
                    value={this.state.email}
                    onChange={this.handleChange('email')}
                    type="email"
                    margin="normal"
                />
                <TextField
                    label="Password"
                    value={this.state.password}
                    type="password"
                    onChange={this.handleChange('password')}
                    margin="normal"
                />
                <TextField
                    label="Fecha de Nacimiento"
                    value={this.state.fechaNacimiento}
                    InputLabelProps={{
                        shrink: true,
                    }}
                    onChange={this.handleChange('fechaNacimiento')}
                    type="date"
                    margin="normal"
                />
                <TextField
                    label="Telfono"
                    value={this.state.telefono}
                    onChange={this.handleChange('telefono')}
                    type="number"
                    margin="normal"
                />
                <TextField

                    label="Pais"
                    value={this.state.pais}
                    onChange={this.handleChange('pais')}
                    margin="normal"
                />
                <ButtonSelectClub
                    club={this.state.club}
                    clubes={this.state.clubes || []}
                    handleClub={(e) => { this.set({ club: e.target.value }) }}
                />

                <Button onClick={this.handleRegister} variant="contained">Registrar</Button>

            </form>
        );
    }
}



export default (FormStaff);