import React from 'react';
import Sync from 'react-syncing';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';


class ButtonAppBar extends Sync {

  render() {
    return (
      <div style={{ backgroundColor: "grey", flexGrow: "1" }}>
        <AppBar position="static">
          <Toolbar>
            <IconButton style={{ marginLeft: "-12" }} color="inherit" aria-label="Menu">
              <MenuIcon onClick={() => this.set({
                left: open,
              })} />
            </IconButton>
            <Typography variant="title" color="inherit" style={{ flex: "1" }}>
              Men
              </Typography>
            {this.state.logged && <Button onClick={() => {
              const url = `http://localhost:8000/logout`;

              fetch(url, {
                method: 'get',
                credentials: "include",
                headers: {
                  'Content-Type': 'application/json'
                },
              })
                .then((res) => res.json())
                .then((res) => {

                  if (res.message) {
                    this.set({ logged: false })
                  }
                })
                .catch(console.log)
            }} color="inherit">Logout</Button>}
          </Toolbar>
        </AppBar>
      </div>
    );
  }

}



export default ButtonAppBar;