import React from 'react';
import Sync from 'react-syncing';
import axios from 'axios';


export default class ProfilePage extends Sync {

    constructor() {
        super()

        this.getProfileStaff = this.getProfileStaff.bind(this);
        this.state = {
            logged: true,
            rol: '',
            user: {},

        }
    }

    didMount() {
        this.getProfileStaff()
    }

    getProfileStaff() {
        const url = `http://localhost:8000/vistaperfil-staff`;
        fetch(url, {
            method: 'GET',
            credentials: "include",
            headers: {
                'Content-Type': 'application/json'
            }

        })
            .then((res) => res.json())
            .then(res => {

                this.set({
                    user: res.result[0]
                })

            })
            .catch(error => {
                console.log(error)
            })
    }

    render() {
        return (

            <div>
                {this.state.user.rol === 1 ? (
                    <div>
                        <p>{this.state.user.nombre}</p>
                        <p>{this.state.user.apellidos}</p>
                    </div>
                ) : (
                        <div>
                            <p>{this.state.user.nombre}</p>
                            <p>{this.state.user.apellidos}</p>
                            <p>{this.state.user.posicion}</p>
                        </div>
                    )}


            </div>


        );
    }
}