import React from 'react';
import Sync from 'react-syncing';
import { withStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import ButtonTablePlayer from './ButtonTablePlayer';





let id = 0;
function createData(name, calories, fat, carbs, protein) {
    id += 1;
    return { id, name, calories, fat, carbs, protein };
}

const rows = [
    createData('Frozen yoghurt', 159, 6.0, 24, 4.0),
    createData('Ice cream sandwich', 237, 9.0, 37, 4.3),
    createData('Eclair', 262, 16.0, 24, 6.0),
    createData('Cupcake', 305, 3.7, 67, 4.3),
    createData('Gingerbread', 356, 16.0, 49, 3.9),
];

export default class TeamStaff extends Sync {
    constructor() {
        super()

        this.getPlayerClub = this.getPlayerClub.bind(this);

        this.state = {
            players: [],
            player: {}
        }
    }

    didMount() {
        this.getPlayerClub()
    }

    getPlayerClub() {
        const url = `http://localhost:8000/team`;
        fetch(url, {
            method: 'GET',
            credentials: "include",
            headers: {
                'Content-Type': 'application/json'
            }

        })
            .then((res) => res.json())
            .then(res => {

                this.set({
                    players: res.result
                })
                console.log(res.result);
            })
            .catch(error => {
                console.log(error)
            })
    }
    render() {

        return (
            <Paper style={{
                width: '100%',
                marginTop: '100px',
                overflowX: 'auto',
            }}>
                <Table style={{ minWidth: 700 }} key={id}>
                    <TableHead>
                        <TableRow>
                            <TableCell>Nombre</TableCell>
                            <TableCell numeric>Posicin</TableCell>
                            <TableCell numeric>Altura</TableCell>
                            <TableCell numeric>Peso</TableCell>
                            <TableCell numeric></TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {rows.map(row => {
                            return (
                                <TableRow style={{ backgroundColor: "default" }} key={row.id} >
                                    <TableCell component="th" scope="row">
                                        {row.name}
                                    </TableCell>
                                    <TableCell numeric>{row.calories}</TableCell>
                                    <TableCell numeric>{row.fat}</TableCell>
                                    <TableCell numeric>{row.carbs}</TableCell>
                                    <TableCell numeric><ButtonTablePlayer /></TableCell>
                                </TableRow>
                            );
                        })}
                    </TableBody>
                </Table>
            </Paper>
        );
    }
}






