import React from 'react';
import Sync from 'react-syncing'
import ButtonMenuHomeStaff from './ButtonMenuHomeStaff';
import ButtonMenuHomePlayer from './ButtonMenuHomePlayer';

export default class Home extends Sync {

    constructor() {
        super()

        this.state = {
            logged: true,
            user: {},
            rol: ''

        }

    }
    didMount() {
        this.getProfileStaff()
    }

    getProfileStaff() {
        const url = `http://localhost:8000/vistaperfil-staff`;
        fetch(url, {
            method: 'GET',
            credentials: "include",
            headers: {
                'Content-Type': 'application/json'
            }

        })
            .then((res) => res.json())
            .then(res => {

                this.set({
                    user: res.result[0]
                })

            })
            .catch(error => {
                console.log(error)
            })
    }

    render() {
        return (
            <div>
                {this.state.user.rol === 1 ? (
                    <ButtonMenuHomeStaff />
                ) : (
                        <ButtonMenuHomePlayer />
                    )}
            </div>
        );
    }
}



