import React from 'react';
import Sync from 'react-syncing'
import { TextField, Button, MenuItem } from '@material-ui/core';
import RadioButtonSleepQuality from './RadioButtonSleepQuality';
import RadioButtonRPE from './RadioButtonRPE';

const sesion = [
    {
        value: 'morning',
        label: 'Maana',
    },
    {
        value: 'afternoon',
        label: 'Tarde',
    },
    {
        value: 'game',
        label: 'Partido',
    },
];


class FormTrainingPlayer extends Sync {

    constructor() {
        super()
        this.handleChange = this.handleChange.bind(this);
        this.handleRegisterTraining = this.handleRegisterTraining.bind(this);


        this.state = {
            date: '',
            session: '',
            sleepQuality: '',
            RPE: '',
            info: '',
            trainingId: ''
        }
    }

    handleRegisterTraining() {
        const url = `http://localhost:8000/addTraining`;

        fetch(url, {
            method: 'post',
            credentials: "include",
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                date: this.state.date,
                session: this.state.session,
                sleepQuality: this.state.sleepQuality,
                RPE: this.state.RPE,
                info: this.state.info,

            })
        })
            .then((res) => res.json())
            .then((res) => {
                if (res.message) {
                    this.set({ logged: true })
                } else {
                    this.set({ error: true, errorMsg: res.error })
                }
            })
            .catch(console.log)
    }


    handleChange(name) {
        return event => {
            this.set({
                [name]: event.target.value,
            });
        };
    }

    render() {


        return (
            <form style={{ display: 'flex', flexWrap: 'wrap' }}>


                <TextField
                    select
                    label="Sesin"
                    value={this.state.session}
                    onChange={this.handleChange('session')}

                    helperText="Seleccion tipo de sesin"
                    margin="normal"
                >
                    {sesion.map(option => (
                        <MenuItem key={option.value} value={option.value}>
                            {option.label}
                        </MenuItem>
                    ))}
                </TextField>
                <RadioButtonSleepQuality />

                <RadioButtonRPE />

                <TextField
                    label="Sentiste algn dolor? Di dnde"
                    multiline
                    rowsMax="4"
                    value={this.state.info}
                    onChange={this.handleChange('info')}
                    margin="normal"
                    helperText="Punta de 1 a 10 Algn dolor o molestia durante o al terminar la sesin?"
                />

                <Button onClick={this.handleRegisterTraining} variant="contained">Registrar</Button>

            </form >
        );
    }
}



export default (FormTrainingPlayer);